import React from 'react';
import { Carousel } from '@trendyol-js/react-carousel';
import { Button, Container, Row, Col } from 'react-bootstrap';
import CarouselCard from './CarouselCard';

function Review() {
    const REVIEWS = [
        {
            name: 'Maison Viano',
            description:
                "Support did a great job helping me through my user error. Explained everything in great detail and I was able to use multiple pixels across different sites all within the same ad account. The Shopify setup only limits you to 1 business account connected to 1 store. This forces you to set up a new Business Manager for each website which isn't ideal.",
            date: 'December 10, 2021',
        },
        {
            name: 'BunnyeBun',
            description:
                'You will need Omega Multiple TikTok Pixels if you have multiple ad accounts with different pixels. It is very easy to use, and the customer service is excellent. Joyce assisted me today. She was very helpful and friendly. Do you have a hundred questions? She will provide you with an answer as soon as possible. Great app and great service.',
            date: 'January 17, 2022',
        },
        {
            name: 'GetPanoMirrors',
            description:
                "I feel like I know the customer support team now :) I've had lots of help from them and customer service is AMAZING. They just make it super easy for me to get setup and troubleshoot any problems I have. Also, they stay up to date with official tiktok reps on making sure we have the best tracking!! 10/10 recommend this to anyone!",
            date: 'March 16, 2022',
        },
        {
            name: 'CUSTOM GIFS SHOP',
            description:
                'Thank you very much, I spent a few days and you solved my problem in just a few minutes. I want to pay tribute to you, this is the team with the fastest response speed and the most timely problem solving we have seen, and the integration of the real-time chat system is really great, thank you! maidedor.com',
            date: 'October 12, 2021',
        },
        {
            name: 'Active Awesome',
            description:
                "Excellent support staff. Kaze & Henry & Mia went above and beyond to help me with a special issue on the app. it's greatly appreciated the work they helped me with. i'll probably be keeping this app for over a year just because of the close support. thanks everyone on the team for the support!",
            date: 'March 2, 2022',
        },
    ];
    // y = f(x) = ax + b
    // f(1) = a + b = 400
    // f(2) = 2a + b = 820
    // f(2.5) = 960
    // a = 420; b = 60
    // 1:2 <=> 400:820
    // 2:2.5 <=> 820:960

    const breakpoint = document.getElementById('root').clientWidth
    let show = 2.5
    if (breakpoint <= 400)
        show = 1
    else if (breakpoint < 820) // 1 -> 2
        show = breakpoint / 400
    else if (breakpoint === 820)
        show = 2
    else if (breakpoint < 960) // 2 -> 2.5
        show = breakpoint / 400

    const handleViewMore = () => {
        window.open(
            'https://apps.shopify.com/tiktok-multi-pixels?ref=viewmore&surface_type=viewmore&surface_detail=viewmore#reviews'
        );
    };

    return (
        <div className="review-section__layout">
            <Container className='review-section__container' fluid='sm'>
                <h4 className="review-section__title">HAPPY USERS - THEIR WORDS, NOT OURS </h4>
                <Row>
                    <Col>
                        <Carousel
                            className="review-section__carousel"
                            show={show}
                            slide={1}
                            transition={0.5}
                            swiping={true}
                        // responsive={true}

                        >
                            {REVIEWS.map((item, i) => (
                                <CarouselCard key={i} {...item} />
                            ))}
                        </Carousel>
                    </Col>
                </Row>
                <Row>
                    <Col className='d-flex justify-content-center mt-3'>
                        <Button className="review-section__button-view-more" variant="light" onClick={handleViewMore}>
                            View more
                        </Button>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Review;
