import React from 'react';
import FlipCountdown from '@rumess/react-flip-countdown';
import {
    Col,
    Container,
    Row,
} from 'react-bootstrap';

const nextDay = new Date().getTime() + 24 * 2 * 60 * 60 * 1000;
const getEndDate = new Date(nextDay).toJSON().slice(0, 10)

export default function Footer({ handleClaimGift }) {
    return (
        <>
            <div className="footer-section__layout">
                <Container>
                    <Row>
                        <Col className="mt-3 mb-3" xs={12} md={7}>
                            <div className="footer-left d-flex flex-column align-items-center justify-content-center">
                                <p className="footer-title">Offer ends in</p>
                                <div className="countdown__wrapper">
                                    <FlipCountdown
                                        dayTitle="Days"
                                        hourTitle="Hours"
                                        minuteTitle="Minutes"
                                        secondTitle="Seconds"
                                        titlePosition="bottom"
                                        size="small"
                                        theme="light"
                                        hideYear
                                        hideMonth
                                        endAt={getEndDate} // yyyy/mm/dd
                                    />
                                </div>
                                <p className="small-text">
                                    ** The app already comes multiple pixels and 1
                                    free Events API, you'll get <b>1 more</b> for
                                    free.
                                </p>
                            </div>
                        </Col>
                        <Col className="mt-3 mb-5" xs={12} md={5}>
                            <div className="d-flex flex-column align-items-center justify-content-center">
                                <p className="footer-title">Limited offer!</p>
                                <button className="claim-gift-button" onClick={handleClaimGift}>
                                    <img className="claim-gift-button__img" src='https://tiktok.omegatheme.com/static/media/gift.2fa15e6c44d45f7628745f41d6231919.svg' alt=""></img>
                                    <p className="claim-gift-button__text">Claim gift!</p>
                                </button>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </div>
            
        </>
    );
}