import React from 'react';

const CarouselCard = ({ name = '', description = '', date = '' }) => {
	return (
		<div className="carouse-card">
			<div className="carouse-card__name">{name}</div>
			<div className="carouse-card__description">{description}</div>
			<div className="carouse-card__date">{date} <img src='https://tiktok.omegatheme.com/static/media/5-stars.6d59de315fdb4fe887a1.png' alt='starts'/></div>
		</div>
	);
};

export default CarouselCard;
