import { useState } from "react";
import { Col, Row, Stack, Tooltip, OverlayTrigger } from "react-bootstrap";
import PopupVideoModal from "./PopupVideoModal";


export default function Header({ handleClaimGift }) {
    const [showNormalBell, setShowNormalBell] = useState(false);
    const [openVideoModal, setOpenVideoModal] = useState(false);


    const handleClickBell = () => {
        setShowNormalBell(true);
    };

    const handleOpenVideoModal = () => {
        setOpenVideoModal(true)
    }

    return (
        <Row className="header ms-0">
            <Col sm='12' md='8'>
                <Stack className="stack align-items-center mt-4">
                    <div className="liner-border mt-1 mb-1">
                        <div style={{textAlign:  'center'}}>
                            FREE FOREVER 2 EVENTS API
                            <OverlayTrigger
                                key='bottom'
                                placement='bottom'
                                overlay={
                                    <Tooltip id='tooltip-bottom'>
                                        Install today, tell us your store URL on the other side to get another 1 MONTH FREE~
                                    </Tooltip>
                                }
                            >
                                <img src={`https://tiktok.omegatheme.com/assets/images/${showNormalBell ? 'bell.png' : 'bell-noti.png'}`} alt="bell" className="bell-img ms-2" onPointerOver={handleClickBell}></img>
                            </OverlayTrigger>

                        </div>
                    </div>
                    <p className="stack-title mb-0">TikTok Pixel App</p>
                    <p className="stack-sub-title">Exclusively reserved for Omega's app users!</p>
                    <button className="claim-gift-button" onClick={handleClaimGift}>
                        <img className="claim-gift-button__img" src='https://tiktok.omegatheme.com/static/media/gift.2fa15e6c44d45f7628745f41d6231919.svg' alt=""></img>
                        <p className="claim-gift-button__text">Claim gift!</p>
                    </button>
                </Stack>
            </Col>
            <Col sm='12' md='4'>
                <div className="video-thumbnail mt-3 me-1">
                    <button className="video-thumbnail__play-button" onClick={handleOpenVideoModal}>
                        <img className="video-thumbnail__play-icon" src="data:image/svg+xml,%3csvg viewBox='0 0 38 38' xmlns='http://www.w3.org/2000/svg' fill-rule='evenodd' clip-rule='evenodd'%3e%3cpath d='M19 1C9.06 1 1 9.057 1 19c0 9.94 8.057 18 18 18 9.94 0 18-8.057 18-18 0-9.94-8.057-18-18-18z' fill='white'/%3e%3cpath d='M19 1C9.06 1 1 9.057 1 19c0 9.94 8.057 18 18 18 9.94 0 18-8.057 18-18 0-9.94-8.057-18-18-18z' fill='none' stroke='%23b5b5b5'/%3e%3cpath d='M15 11.723c0-.605.7-.942 1.173-.564l10.93 7.215a.72.72 0 010 1.128l-10.93 7.216A.723.723 0 0115 26.153v-14.43z' fill-opacity='.557'/%3e%3c/svg%3e" alt="" />
                    </button>
                    <p className="video-thumbnail__timestamp">0:39</p>
                </div>

                <PopupVideoModal
                    open={openVideoModal}
                    onClose={() => {
                        setOpenVideoModal(false);
                    }}
                    src="https://www.youtube.com/embed/pICCkyNaIJU"
                />
            </Col>
        </Row>
    )
}